<script>
export default {
	name: 'rating-counter',
  props: {
    counter: {
      type: Object,
      required: true
    }
  },
  methods: {
    toggleLike() {
      if (this.counter.isVoted == undefined) {
        this.counter.likes += 1;
        this.counter.isVoted = true;
      }
      else {
        if (!this.counter.isVoted) {
          this.counter.dislikes -= 1;
          this.counter.likes += 1;
          this.counter.isVoted = true;
        }
        else {
          this.counter.likes -= 1;
          this.counter.isVoted = undefined;
        }
      }
    },
    toggleDislike() {
      if (this.counter.isVoted == undefined) {
        this.counter.dislikes += 1;
        this.counter.isVoted = false;
      }
      else {
        if (this.counter.isVoted) {
          this.counter.dislikes += 1;
          this.counter.likes -= 1;
          this.counter.isVoted = false;
        }
        else {
          this.counter.dislikes -= 1;
          this.counter.isVoted = undefined;
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
	/*RATING*/
	.rating {
			&__body {
				display: flex;
				justify-content: center;
				align-items: center;
				gap: 50px;
			}
			&__counter {
				display: flex;
				align-items: center;
				gap: 10px;
				height: 70px;
			}
			&__image {
				width: 50px;
				cursor: pointer;
			}
			&__text {
				font-size: 50px;
			}
	}
	.like {
		transition: 0.3s all 0s;
		&:hover {
			transform: scale(1.2);
			fill: rgb(127, 182, 255);
		}
    &.active {
      fill: rgb(127, 182, 255);
    }
	}
	.dislike {
		transition: 0.3s all 0s;
		&:hover {
			transform: scale(1.2);
			fill: rgb(119, 7, 7);
		}
    &.active {
      fill: rgb(119, 7, 7);
    }
	}
</style>